import React from 'react';
import './inputForm.css'

class Upload extends React.Component {
    state = {
        file: '',
        time: '',
        date: '',
    }
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }
    handleChangeImg = (e) => {
        if (e.target.files.length) {
            this.setState({
                preview: URL.createObjectURL(e.target.files[0]),
                file: e.target.files[0]
            })
        }
    }
    clearImg = () => {
        this.setState({
            preview: '',
            file: ''
        })
    }
    handleUpload = () => {
        const {file, time, date, preview} = this.state;

        const userId = localStorage.getItem("userId")
        const first_5_lets_userId = userId.slice(0, 5)
        const iName = String(first_5_lets_userId + "_" + file.name)
    
        const formData = new FormData();
        formData.append('image', file, iName)
        console.log(date, time)
        setTimeout(() => {
        // fetch('https://multer-test123.herokuapp.com/upload', {
        //     method: 'POST',
        //     body: formData
        // })
        // .then(res => res.json())
        // .then(data => {
        //     console.log(data)
        // })
        }, 2000)
    }
    render() {
        const {file, time, date, preview} = this.state;
        return (
            <React.Fragment>
                <div className="formFlex">
                <div className="inputForm">
                    <label for="date">Date:</label>
                    <input type="date" id="date" value={date} onChange={this.handleChange} />
                    <label for="time">Time:</label>
                    <input type="time" id="time" value={time} onChange={this.handleChange} />


                    <label style={file !== '' ? {height: "150px"} : {height: "75px"}} className="fileBox" for="file">
                    {preview ? (
                        <React.Fragment>
                        <img src={preview} width="100" height="100" />
                        <button 
                        onClick={this.clearImg}
                        className="clearImg">Change image</button>
                        </React.Fragment>
                    ) : (
                        <div className="uploadBtn">Please select an image</div>
                    )}
                    </label>
                    <input type="file" id="file" style={{display: "none"}} onChange={this.handleChangeImg} />
                    <button 
                    className="btn"
                    onClick={this.handleUpload}
                    >Upload</button>
                </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Upload;